'use client';

import { Checkbox } from '@/components/ui/checkbox';
import { useAuth } from '@/context/AuthContext';
import { Question } from '@/types.d';
import { Bookmark } from 'lucide-react'; // Using a popular icon library
import { DifficultyTag } from './DifficultyTag';

const QuestionItem = ({ question }: { question: Question }) => {
  const {
    isAuthenticated,
    user,
    toggleQuestionProgress,
    toggleQuestionBookmark,
  } = useAuth();

  // Check if the current question is in the user's completed or bookmarked lists
  const isCompleted =
    user?.completedQuestions?.some((q) => q._id === question._id) ?? false;
  const isBookmarked =
    user?.bookmarkedQuestions?.some((q) => q._id === question._id) ?? false;

  // Prevent the link from navigating when clicking the checkbox/bookmark
  const handleCheckboxClick = (e: React.MouseEvent) => {
    e.preventDefault();
    toggleQuestionProgress(question._id);
  };

  const handleBookmarkClick = (e: React.MouseEvent) => {
    e.preventDefault();
    toggleQuestionBookmark(question._id);
  };

  const hasUrl = question.url && question.url.length > 0;

  return (
    <a
      href={hasUrl ? question.url[0] : '#'}
      target={hasUrl ? '_blank' : '_self'}
      rel="noopener noreferrer"
      className="flex items-center justify-between p-4 bg-muted/40 hover:bg-muted/80 rounded-lg transition-colors duration-200 group"
    >
      <div className="flex items-center gap-4">
        {isAuthenticated && (
          <div
            onClick={handleCheckboxClick}
            className="flex items-center h-full cursor-pointer"
          >
            <Checkbox checked={isCompleted} />
          </div>
        )}
        <span className="text-card-foreground text-base md:text-lg">
          {question.title}
        </span>
      </div>
      <div className="flex items-center gap-4">
        <DifficultyTag difficulty={question.difficulty} />
        {isAuthenticated && (
          <button
            onClick={handleBookmarkClick}
            title="Toggle Bookmark"
            className="rounded-full p-1"
          >
            <Bookmark
              className={`w-5 h-5 transition-all ${
                isBookmarked
                  ? 'text-yellow-400 fill-yellow-400'
                  : 'text-muted-foreground group-hover:text-foreground'
              }`}
            />
          </button>
        )}
      </div>
    </a>
  );
};

export default QuestionItem;
