// useSpeechRecognition.ts
import { useEffect, useState } from 'react';

// Define the return type for clarity and type safety
interface SpeechRecognitionResult {
  text: string;
  isListening: boolean;
  isSupported: boolean;
  startListening: () => void;
  stopListening: () => void;
  clearText: () => void;
  error: string | null;
}

const useSpeechRecognition = (): SpeechRecognitionResult => {
  const [isListening, setIsListening] = useState(false);
  const [text, setText] = useState('');
  const [error, setError] = useState<string | null>(null);

  // Check for browser support
  const SpeechRecognition =
    window.SpeechRecognition || window.webkitSpeechRecognition;
  const isSupported = !!SpeechRecognition;

  useEffect(() => {
    if (!isSupported) {
      setError('Web Speech API is not supported in this browser.');
      return;
    }

    const recognition = new SpeechRecognition();
    recognition.continuous = true; // Keep listening after a result is returned
    recognition.interimResults = false; // Only return final, confirmed results
    recognition.lang = 'en-US';

    recognition.onresult = (event) => {
      let interimTranscript = '';
      for (let i = event.resultIndex; i < event.results.length; i++) {
        const transcript = event.results[i][0].transcript;
        if (event.results[i].isFinal) {
          setText(transcript);
          // Stop listening after a final result is processed
          // This prevents the hook from continuously updating state
          recognition.stop();
        } else {
          interimTranscript += transcript;
        }
      }
    };

    recognition.onstart = () => {
      setIsListening(true);
      setError(null);
    };

    recognition.onend = () => {
      setIsListening(false);
    };

    recognition.onerror = (event) => {
      setError(`Speech recognition error: ${event.error}`);
      setIsListening(false);
    };

    // Clean up the recognition instance when the component unmounts
    return () => {
      recognition.stop();
    };
  }, [isSupported]);

  const startListening = () => {
    if (isSupported) {
      setText(''); // Clear previous text
      window.SpeechRecognition.start(); // This line may require a different call depending on browser specifics, but this is a common approach.
    }
  };

  const stopListening = () => {
    if (isSupported) {
      window.SpeechRecognition.stop();
    }
  };

  const clearText = () => {
    setText('');
  };

  return {
    text,
    isListening,
    isSupported,
    startListening,
    stopListening,
    clearText,
    error,
  };
};

export default useSpeechRecognition;
